.btn-key {
  width: 33px;
  margin: 1px;
  padding: 0;
}

@media (min-width: 576px) {
  .btn-key {
    width: 50px;
  }

  .keyrow-0 {
    margin-left: 75px;
  }

  .keyrow-1 {
    margin-left: 50px;
  }

  .keyrow-2 {
    margin-left: 25px;
  }

  .keyrow-3 {
    margin-left: 0px;
  }
}



